.site-header {
	z-index: 99;
	backdrop-filter: blur(20px);
	width: calc(100% - 2em);
	position: fixed;
	display: flex;
	justify-content: space-between;
	align-items: center;
	top: 1em;
	left: 1em;
	border-radius: 6px;
	padding: 1.75em 1.5em;
	transition: .3s $transition-smooth background-color, 0s ease backdrop-filter;
	background: none;

	&-sticky {
		background: rgba(lighten($color-body, 3), .8);
	}

	.header-title {
		position: relative;
		opacity: 0;

		a {
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			font-size: 1.75em;
			text-decoration: none;
			color: #fff;
			width: 6em;
			line-height: .75;
			font-weight: 600;
			text-indent: 1rem;
			margin-top: -0.25rem;

			&:before {
				content: '';
				width: 0;
				height: 3px;
				background: #fff;
				position: absolute;
				bottom: -12px;
				left: 0;
				z-index: -1;
				transition: .3s $transition-reveal;
			}

			&:hover {
				color: $color-toxic;

				&:before {
					width: 100%;
				}
			}
		}
	}

	@media (max-width: $breakpoint-medium) {
		top: .5em;
	}
}